import btnComponent from '@/components/btnComponent.vue';

export default {
	components: {
		btnComponent
	},
	data() {
		return {
			article: this.$app.getData('config').funclub_notice,

			tmpImg: {
				avatar: null,
				qualify: null,
			}
		};
	},
	onLoad(option) {
		uni.setNavigationBarTitle({
			title: option.starname + '后援会入驻'
		})
	},

	methods: {
		formSubmit(e) {
			const value = e.detail.value

			this.$app.upload([this.tmpImg.avatar, this.tmpImg.qualify], res => {

				value['avatar'] = res[0]
				value['qualify'] = res[1]
				this.$app.request(this.$app.API.EXT_FUNCLUB_JOIN, value, res => {
					this.$app.toast('提交成功！')

				})
			})
		},
		uploadImg(name) {
			uni.chooseImage({
				count: 1, //默认9
				success: res => {
					this.tmpImg[name] = res.tempFilePaths[0]
				}
			});
		},

	}
}
